.card {
    margin-top: 30px;
    margin-left: 30px;
    padding-bottom: 30px;
    width: 500px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #F5F5F5;
}

.card-img {
    /* width: 125px;
    height: 168px; */
    border-radius: 5px;

}

.card-icon {
    font-size: 12px;
}

.card-country {
    margin-left: 5px;
    font-weight: 400;
    font-size: 10.24px;
    line-height: 12px;
    letter-spacing: 0.17em;

    color: #2B283A;
}

.card-link {
    margin-left: 50px;
    font-weight: 400;
    font-size: 10.24px;
    line-height: 12px;
    /* identical to box height */
    text-decoration-line: underline;

    color: #918E9B;
}

.card-desc {
width: 326px;
line-height: 150%;
color: #2B283A;
}

.card-info {
    display: flex;
    /* justify-content: space-between; */
}

.card-location {
    display: flex;
}